"use strict";
const coregulp = require('gulp');
const chalk = require('chalk');
const _ = require('lodash');
/**
 * Task base class.
 *
 * @export
 * @class Task
 * @implements {ITask}
 */
class PipeTask {
    constructor(info) {
        this.decorator = info || {};
    }
    pipes(config, dist, gulp) {
        let option = config.option;
        let loader = option.loader;
        if (loader && _.isFunction(loader.pipes)) {
            return _.isFunction(loader.pipes) ? loader.pipes(config, option, gulp) : _.filter(loader.pipes, p => _.isFunction(p) || (p.name && p.name === dist.name));
        }
        else {
            return [];
        }
    }
    output(config, dist, gulp) {
        let option = config.option;
        let loader = option.loader;
        if (loader && !_.isString(loader) && !_.isArray(loader)) {
            if (loader.output) {
                return _.isFunction(loader.output) ? loader.output(config, option, gulp) : _.filter(loader.pipes, p => _.isFunction(p) || (p.name && p.name === dist.name));
            }
            else if (loader.output === null) {
                return [(stream) => stream];
            }
        }
        return [(stream) => stream.pipe(gulp.dest(config.getDist(dist)))];
    }
    getOption(config) {
        return config.option;
    }
    sourceStream(config, option, gulp) {
        return gulp.src(config.getSrc(option, this.decorator));
    }
    setup(config, gulp) {
        gulp = gulp || coregulp;
        let option = this.getOption(config);
        let tk = config.subTaskName(option, this.name);
        console.log(`register ${this.name} task:`, chalk.cyan(tk));
        gulp.task(tk, () => {
            return Promise.resolve(this.sourceStream(config, option, gulp))
                .then(psrc => {
                return Promise.all(_.map(this.pipes(config, option, gulp), (p) => {
                    if (_.isFunction(p)) {
                        return p(config, option, gulp);
                    }
                    else {
                        return Promise.resolve(p.toTransform(config, option, gulp))
                            .then(trs => {
                            trs.order = p.order;
                            return trs;
                        });
                    }
                }))
                    .then(tans => {
                    let len = tans.length;
                    tans = _.orderBy(_.filter(tans, t => t), t => {
                        if (_.isArray(t)) {
                            return len;
                        }
                        else {
                            if (_.isNumber(t.order)) {
                                return t.order;
                            }
                            return len;
                        }
                    });
                    _.each(tans, stream => {
                        psrc = psrc.pipe(stream);
                    });
                    return psrc;
                });
            })
                .then(stream => {
                let outputs = this.output(config, option, gulp);
                return Promise.all(_.map(outputs, output => {
                    if (_.isFunction(output)) {
                        return output(stream, config, option, gulp);
                    }
                    else {
                        return output.toTransform(stream, config, option, gulp);
                    }
                }));
            }).then(outputs => {
                return Promise.all(_.map(outputs, output => {
                    return new Promise((resolve, reject) => {
                        output
                            .once('end', () => {
                            resolve(output);
                        })
                            .once('error', reject);
                    });
                }));
            })
                .catch(err => {
                console.log(chalk.red(err));
                process.exit(0);
            });
        });
        this.decorator.taskName = tk;
        return tk;
    }
}
exports.PipeTask = PipeTask;
